/**
 *
 * @title ref-tree 参照-树形
 * @description 具有单选多选的树形参照
 *
 */

import { FormattedMessage } from 'react-intl';
import React, { Component } from 'react';
import PapReferOrg from 'pap-refer/lib/pap-ref-org/src/index';
// import 'pap-refer/lib/pap-ref-org.css';
import PapReferDeptUnderOrg from 'pap-refer/lib/pap-ref-deptUnderOrg/src/index';
// import 'pap-refer/lib/pap-ref-deptUnderOrg';
import { Button, Form, Panel } from 'tinper-bee';
import Card from '../Card'
let code =
`
import React, { Component } from 'react';
import PapReferOrg from 'pap-refer/lib/pap-ref-org';
import 'pap-refer/lib/pap-ref-org.css';
import PapReferDept from 'pap-refer/lib/pap-ref-dept';
import 'pap-refer/lib/pap-ref-dept.css';
import { Button, Form, Panel } from 'tinper-bee';
import Card from '../Card'
class Demo4 extends Component {
  constructor() {
    super();
    this.state = {
      value: '',
      singleClientParam: {},
    }

  }
  singleSave = (result) => {
    //组织单选的保存，级联参照
    this.setState({
      singleClientParam: result.length === 0 ? {} : { 'organization_id': result[0].refpk },
    })

  }
  render() {

    const { getFieldProps, getFieldError } = this.props.form;

    let { singleClientParam } = this.state;
    return (
      <Card
        title={this.props.intl.formatMessage({id:"js.Ref.Ref2.0001", defaultMessage:"参照级联"})}
        codeText={code}
        intl={this.props.intl}
        footer={
          <Button colors="primary"
            style={{
              margin: 'auto 5px',
              height: '30px',
              padding: '0px'
            }}
            onClick={() => {
              this.props.form.validateFields((err, values) => {
                if (err) return;
                alert(this.props.intl.formatMessage({id:"js.Ref.Ref6.0007", defaultMessage:"您选择的是"})+JSON.stringify(values))
              });
            }}
          >
            <FormattedMessage id="js.Ref.Ref2.0003" defaultMessage="提交" />
           </Button>
        }
      >
        <div className="demo-label">
          <span ><FormattedMessage id="js.Ref.Ref2.0004" defaultMessage="组织：" /></span>
          <PapReferOrg
            multiple={false}
            searchable={true}
            checkStrictly={true}
            strictMode={true}
            displayField='{refname}'
            valueField='refpk'
            onSave={this.singleSave}
           lang={this.props.lang}
            theme={this.props.theme}
            emptyBut={true}
            {...getFieldProps('code', {
              initialValue: '{"refname":"","refpk":""}',
              rules: [{
                message: <FormattedMessage id="js.Ref.Ref2.0005" defaultMessage="提示：请选择" />,
                pattern: /[^{"refname":"","refpk":""}|{"refpk":"","refname":""}]/
              }]
            })}
          />
          <span style={{
            color: 'red'
          }}>
            {
              getFieldError('code')
            }
          </span>
        </div>
        <div className="demo-label">
          <span ><FormattedMessage id="js.Ref.Ref2.0006" defaultMessage="部门：" /></span>
          <PapReferDept
            multiple={false}
            searchable={true}
            checkStrictly={true}
            strictMode={true}
            displayField='{refname}'
            valueField='refpk'
           lang={this.props.lang}
            theme={this.props.theme}
            emptyBut={true}
            {...getFieldProps('code1', {
              initialValue: '{"refname":"","refpk":""}',
              rules: [{
                message: <FormattedMessage id="js.Ref.Ref2.0005" defaultMessage="提示：请选择" />,
                pattern: /[^{"refname":"","refpk":""}|{"refpk":"","refname":""}]/
              }]
            })}
          />

          <span style={{
            color: 'red'
          }}>
            {
              getFieldError('code1')
            }
          </span>
        </div>
      </Card>
    )
  }
};

`
class Demo4 extends Component {
  constructor() {
    super();
    this.state = {
      value: '',
      singleClientParam: {},
      jiLianValue: {
        refname: "",
        refpk: "",
      },
      sum:1,
    }

  }
  singleSave = (result) => {
    //组织单选的保存，级联参照
    this.setState({
      jiLianValue: Object.assign({}, { "refname": "", "refpk": this.state.sum+'' }),//更换完清空级联的数据
      sum: ++this.state.sum,
      singleClientParam: result.length === 0 ? {} : { 'organization_id': result[0].refpk },
    })

  }
  render() {

    const { getFieldProps, getFieldError } = this.props.form;

    let { singleClientParam,jiLianValue } = this.state;
    return (
      <Card
        title={this.props.intl.formatMessage({id:"js.Ref.Ref2.0001", defaultMessage:"参照级联"})}
        codeText={code}
        intl={this.props.intl}
        footer={
          <Button colors="primary"
            style={{
              margin: 'auto 5px',
              height: '30px',
              padding: '0px'
            }}
            onClick={() => {
              this.props.form.validateFields((err, values) => {
                if (err) return;
                alert(this.props.intl.formatMessage({id:"js.Ref.Ref6.0007", defaultMessage:"您选择的是"})+JSON.stringify(values))
              });
            }}
          >
            <FormattedMessage id="js.Ref.Ref2.0003" defaultMessage="提交" />
           </Button>
        }
      >
        <div className="demo-label">
          <span ><FormattedMessage id="js.Ref.Ref2.0004" defaultMessage="组织：" /></span>
          <PapReferOrg
            multiple={false}
            searchable={true}
            checkStrictly={true}
            strictMode={true}
            displayField='{refname}'
            valueField='refpk'
            onSave={this.singleSave}
           lang={this.props.lang}
            theme={this.props.theme}
            emptyBut={true}
            {...getFieldProps('code', {
              initialValue: '{"refname":"","refpk":""}',
              rules: [{
                message: <FormattedMessage id="js.Ref.Ref2.0005" defaultMessage="提示：请选择" />,
                pattern: /[^{"refname":"","refpk":""}|{"refpk":"","refname":""}]/
              }]
            })}
          />
          <span style={{
            color: 'red'
          }}>
            {
              getFieldError('code')
            }
          </span>
        </div>
        <div className="demo-label">
          <span ><FormattedMessage id="js.Ref.Ref2.0006" defaultMessage="部门：" /></span>
          <PapReferDeptUnderOrg
            multiple={false}
            searchable={true}
            checkStrictly={true}
            strictMode={true}
            displayField='{refname}'
            valueField='refpk'
           lang={this.props.lang}
            theme={this.props.theme}
            emptyBut={true}
            param={{
                "clientParam":Object.keys(singleClientParam).length===0?'':singleClientParam
                
            }}
            {...getFieldProps('code1', {
              initialValue: JSON.stringify(jiLianValue),
              rules: [{
                message: <FormattedMessage id="js.Ref.Ref2.0005" defaultMessage="提示：请选择" />,
                pattern: /[^{"refname":"","refpk":""}|{"refpk":"","refname":""}]/
              }]
            })}
            value={JSON.stringify(jiLianValue)}
          />

          <span style={{
            color: 'red'
          }}>
            {
              getFieldError('code1')
            }
          </span>
        </div>
      </Card>
    )
  }
};

export default Form.createForm()(Demo4);

